<div class="login" v-if="isLogin"><base-icon class="login__logo" name="logo"></base-icon><h4 class="login__title">Вход в аккаунт</h4><form class="authorization" @submit.prevent="loginSubmit"><div class="authorization__inputs"><base-input class="authorization__input" v-model="loginUser.email" placeholder="Логин"></base-input><base-input class="authorization__input" placeholder="Пароль" v-model="loginUser.password" type="password"></base-input></div><base-button class="authorization__btn" customType="submit">Войти</base-button></form><div class="login__sub"><base-button class="login__registration" customType="fill" @handleClick="changeAuthorizationView()">Регистрация</base-button></div></div><div class="register" v-else><base-icon class="login__logo" name="logo"></base-icon><h4 class="login__title">Регистрация</h4><form class="registration" @submit.prevent="registerSubmit"><div class="registration__inputs"><base-input class="registration__input" v-model="registerUser.email" placeholder="Почта" :error="validation.firstError('registerUser.email')"></base-input><base-input class="registration__input" placeholder="Пароль" v-model="registerUser.password" type="password" :error="validation.firstError('registerUser.password')"></base-input></div><base-button class="registration__btn" customType="submit">Зарегистрироваться</base-button></form><div class="login__sub"><base-button class="login__registration" customType="fill" @handleClick="changeAuthorizationView()">Войти</base-button></div></div>